.quoteblock blockquote::before {
    content: "\201c";
    float: left;
    font-size: 2.75em;
    font-weight: bold;
    line-height: .6em;
    margin-left: -.6em;
    color: #943000;
    text-shadow: 0 1px 2px rgba(0,0,0,.1);
}

// Overrides

table {margin-bottom: 1.5rem;}
table.tableblock {box-sizing:border-box;}
table.tableblock thead th, table.tableblock tbody th {font-size: 0.9rem; padding: 0.5rem;}
table.tableblock tbody tr td {padding: 0.5rem}
table.tableblock tbody tr td p {font-size: 0.9rem}


table.configuration-reference a {text-decoration:none;}
table.configuration-reference { table-layout: inherit;}


#content h1>a.anchor::before,h2>a.anchor::before{padding-top:.3em}
.admonitionblock>table{border-collapse:separate;border:0;background:none;width:auto}
.admonitionblock td.icon {border: none !important; }
.admonitionblock td.content{border-top: none !important; border-right: none !important; border-bottom: none !important;}
.admonitionblock td.icon .icon-note::before{color:$quarkus-blue; }
.admonitionblock td.icon .icon-tip::before{color:#943000;text-shadow:inherit;}
.conum[data-value]{color:black !important; background:#CCC; }
.conum[data-value] *{color:black !important}
.admonitionblock>table td.content { font-weight:400; line-height: 1.5rem; }

.quoteblock blockquote::before {
    content: "\201c";
    float: left;
    font-size: 2.75em;
    font-weight: bold;
    line-height: .6em;
    margin-left: -.6em;
    color: $quarkus-blue;
    text-shadow: 0 1px 2px rgba(0,0,0,.1);
}

// clipboard for code blocks
pre.highlight {
  .btn-copy {
     display: block;
     margin-top: -0.2em;
     margin-bottom: 0;
  }
}
