.events-band {

  .event {
    margin-bottom: 2rem;


      .title, a {
        font-size: 1.25rem;
        font-weight: 600;
        }

      .description, speakers, eventdate  {
        margin: 1rem 0 1.5rem 0;
        font-size: 1rem;
        line-height: 1.3rem;
        }

      .eventdate {
        font-weight: 600;
        }
        

    @media screen and (min-width: 1124px) {
      .grid-wrapper {
        grid-gap: 2em;
      }
    }
    @media screen and (max-width: 992px) {
      grid-column: span 12;
    }
  }
  .img-wrapper {
    grid-column: span 4;
    @media screen and (max-width: 1124px) {
      grid-column: span 6;
    }
    @media screen and (max-width: 480px) {
      grid-column: span 12;
    }
  }
  .info-wrapper {
    grid-column: span 8;
    @media screen and (max-width: 1124px) {
      grid-column: span 6;
    }
    @media screen and (max-width: 480px) {
      grid-column: span 12;
    }
  }
}
